    -webkit-transform:rotate(45deg);
    -ms-transform:rotate(45deg);
    transform:rotate(45deg);
    left:-15px;
    top:15px
}
.pricing-item--style-2 .pricing-item__list li::after{
    height:4px;
    -webkit-transform:rotate(-45deg);
    -ms-transform:rotate(-45deg);
    transform:rotate(-45deg);
    left:-20px;
    top:19px
}
.pricing-item--style-2 .pricing-item__list li.item--disabled{
    color:#bebebe
}
.pricing-item--style-2 .pricing-item__list li.item--disabled::after,.pricing-item--style-2 .pricing-item__list li.item--disabled::before{
    display:none
}
.pricing-item--style-2 .pricing-item__button{
    padding:13px 42px 13px 42px;
    margin-top:22px
}
.pricing-item--style-3{
    padding:37px 15px;
    border-bottom:2px solid #f3f0ee;
    text-align:center
}
@media only screen and (min-width:992px){
    .pricing-item--style-3{
        text-align:left
    }
}
.pricing-item--style-3.pricing-item--selected{
    background-color:#f3f0ee
}
.pricing-item--style-3 .pricing-item__price{
    color:#5e5c8b;
    font-size:32px;
    font-weight:700;
    letter-spacing:-.070em;
    margin:15px 0
}
@media only screen and (min-width:992px){
    .pricing-item--style-3 .pricing-item__price{
        font-size:40px;
        margin:0
    }
}
@media only screen and (min-width:1200px){
    .pricing-item--style-3 .pricing-item__price{
        font-size:40px
    }
}
.pricing--style-4{
    position:relative
}
.pricing--style-4__bg{
    position:absolute;
    top:0;
    left:0;
    right:0;
    bottom:0;
    width:100%;
    height:100%
}
.counter-item__lower{
    font-size:60px;
    line-height:60px;
    font-weight:700;
    letter-spacing:-.070em
}
@media only screen and (min-width:992px){
    .counter-item__lower{
        font-size:80px;
        line-height:80px
    }
}
@media only screen and (min-width:1200px){
    .counter-item__lower{
        font-size:100px;
        line-height:100px
    }
}
.counter-item{
    display:inline-block;
    text-align:center;
    margin-bottom:50px
}
@media only screen and (min-width:768px){
    .counter-item{
        text-align:left
    }
}
.counter-item--style-1 .counter-item__top .icon{
    font-size:66px
}
.counter-item--style-1 .counter-item__title{
    margin-top:13px
}
.counter-item--style-1 .counter-item__lower{
    color:#5e5c8b
}
.counter--style-2{
    position:relative
}
.counter--style-2__bg{
    position:absolute;
    top:0;
    right:0;
    left:0;
    bottom:0;
    width:100%;
    height:100%
}
.counter-item--style-2{
    color:#fff
}
.counter-item--style-2 .counter-item__title{
    color:#fff
}
.counter-item--style-3 .counter-item__lower{
    color:#5e5c8b;
    line-height:1.2
}
